import React from 'react';
import {NavLink} from 'react-router-dom';
import './Card.css';


const Card = ({combProps}) => {
    function click() {
        alert(' !')
    }
    return (
            <div className = 'titleTable' onClick={click}>
                <div className = 'column'>{combProps.country}</div>
                <div className = 'column'>{combProps.client}</div>
                <div className = 'column'>{combProps.mine}</div>
                <div className = 'column'>{combProps.install}</div>
                <div className = 'column'>{combProps.combName}</div>
                <div className = 'column'>{combProps.combNumber}</div>
                <div className = 'column'>{combProps.installDate}</div>
                <div className = 'column'>{combProps.warrantyTerm}</div>
                <div className = 'column'>{combProps.startOperationDate}</div>
                <div className = 'column'><button></button></div>
                <div className = 'column'><button></button></div>
            </div>
    )
};




export default Card;


