import React, {Component, createContext} from 'react';
import moment from 'moment';
import axios from 'axios';

export const UserContext = createContext({
    user: {
        email: null,
        avatar: null,
    },
    onLogin: () => null,
    onLogout: () => null,
    onRegister: () => null,
});


export default class UserContextProvider extends Component {
    static Consumer = UserContext.Consumer;

    state = {
        isLogged: false,
        id: null,
        login: null,
        email: null,
        password: null,
        passwordRepeat: null,
        error: false,
        isAddComb: false,
        country: null,
        client: null,
        mine: null,
        install: null,
        combName: null,
        combNumber: null,
        installDate: null,
        warrantyTerm: null,
        startOperationDate: null,
        combines: [],
        searchValue: null,
        addCombShow: false,
        date: moment(),
    };

    componentDidMount () {
        this.getArrayFromBase()
    }

    getArrayFromBase = () => {
        let arrOfComb = JSON.parse(localStorage.getItem('combines'))
        this.setState ({
            combines: arrOfComb,
        })
    }

    searchComb = (e) => {
        this.setState({
            searchValue: e.target.value
        })
    }

    searchCombInBase = (e) => {
        let someLetter = e.target.value;
        let someSearch = JSON.parse(localStorage.getItem('combines'));
        let searchedArray = someSearch.filter(el => Object.values(el).some(key => key ? key.toLowerCase().includes(someLetter.toLowerCase()) : null))
        this.setState({
            combines: searchedArray,
        })
            }

    onLogin = credentials => {
        let user = JSON.parse(localStorage.getItem("userObject"));
        if (user.login === credentials.login && user.password === credentials.password) {
        // localStorage.setItem("id", id.map(el => el._id));
        localStorage.setItem("isLogged", true);
        this.setState({
            id: localStorage.getItem("id"),
            isLogged: true
        })
        }
        //     axios.get(`http://localhost:3001/api/log/${this.state.email}?password=${this.state.password}`)
        //         .then(info => {
        //             let id = localStorage.getItem("id")
        //             // if (id !== info.data.map(el => el._id))
        //             localStorage.setItem("id", info.data.map(el => el._id));
        //             localStorage.setItem("isLogged", true);
        //             this.setState({
        //                 id: localStorage.getItem("id"),
        //                 isLogged: true
        //             })
        //         })
        //         .catch(err => console.log(err))
    };

    onLogout = (e) => {
        // localStorage.setItem("id", "");
        localStorage.setItem("isLogged", "");
        console.log(localStorage.getItem('isLogged'));
        // localStorage.setItem("period", []);
        this.setState({
            isLogged: false,
            login: null,
            email: null,
            password: null,
            passwordRepeat: null,

        })
    };

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value,
        })
    };

    handleSubmit = e => {
        e.preventDefault();
        console.log(this.state);
        this.onLogin(this.state);
        // this.setState({
        //     isLogged: true,
        // })
    };

    handleSubmitComb = e => {
        e.preventDefault();
        // console.log(this.state);
        let newComb = {
            country: this.state.country,
            client: this.state.client,
            mine: this.state.mine,
            install: this.state.install,
            combName: this.state.combName,
            combNumber: this.state.combNumber,
            installDate: this.state.installDate,
            warrantyTerm: this.state.warrantyTerm,
            startOperationDate: this.state.startOperationDate,
        };
        let combines = JSON.parse(localStorage.getItem("combines"))
        console.log(combines);
        if (combines) {
            localStorage.setItem("combines", JSON.stringify([...combines, newComb]))
            console.log('yes')
        } else {
        localStorage.setItem("combines", JSON.stringify([newComb]))
        console.log('no') }

        this.getArrayFromBase()
        this.setState({
            country: "",
            client: "",
            mine: "",
            install: "",
            combName: "",
            combNumber: "",
            installDate: "",
            warrantyTerm: "",
            startOperationDate: "",
        })
    };

    addNewCombine = () => {
        // localStorage.setItem("isAddComb", true);
        this.setState(prev => ({
            addCombShow: !prev.addCombShow
        }))
    }

    handleChangeInstallDate = (date) => {
        this.setState({
            date: date,
        });
    }

    // onRegister = e => {
    //     e.preventDefault();
    //     if (this.state.password === this.state.passwordRepeat) {
    //         axios.post('http://localhost:3001/api/reg', {
    //             login: this.state.email,
    //             email: this.state.email,
    //             password: this.state.password
    //         })
    //         // .then(function(res){ console.log(res) })
    //         // .catch(function(res){ console.log(res) })
    //         //     .then(info => console.log(info.data))
    //             .then(info => {
    //                 console.log(info.data)
    //                 localStorage.setItem("id", info.data._id);
    //                 localStorage.setItem("isLogged", true);
    //                 this.setState({
    //                     id: localStorage.getItem("id"),
    //                     isLogged: true,
    //                     error: false
    //                 })
    //
    //             })
    //             .catch(err => console.log(err))
    //     } else {
    //         this.setState({
    //             error: true,
    //         })}

// axios.post('#', {email: this.state.email, password: this.state.password} )
// .then(info => this.setState({
//     isLogged: true,
//     error: false
// }))
// .catch(err => console.log(err))
//     }

    render() {
        return (
            <UserContext.Provider
                value={{
                    user: {
                        country: this.state.country,
                        client: this.state.client,
                        mine: this.state.mine,
                        install: this.state.install,
                        combName: this.state.combName,
                        combNumber: this.state.combNumber,
                        installDate: this.state.installDate,
                        warrantyTerm: this.state.warrantyTerm,
                        startOperationDate: this.state.startOperationDate,
                        isAddComb: this.state.isAddComb,
                        isLogged: this.state.isLogged,
                        email: this.state.email,
                        password: this.state.password,
                        passwordRepeat: this.state.passwordRepeat,
                        error: this.state.error,
                        id: this.state.id,
                        combines: this.state.combines,
                        searchValue: this.state.searchValue,
                        addCombShow: this.state.addCombShow,
                        date: this.state.date,
                    },
                    onLogin: this.onLogin,
                    onLogout: this.onLogout,
                    onRegister: this.onRegister,
                    handleChange: this.handleChange,
                    handleSubmit: this.handleSubmit,
                    addNewCombine: this.addNewCombine,
                    handleSubmitComb: this.handleSubmitComb,
                    getArrayFromBase: this.getArrayFromBase,
                    searchComb: this.searchComb,
                    searchCombInBase: this.searchCombInBase,
                    handleChangeInstallDate: this.handleChangeInstallDate,
                }}>
                {this.props.children}
            </UserContext.Provider>
        );
    }
}



