import React, { Component } from 'react';
import {UserContext} from '../UserContext';
import CalendarNew from '../Calendar/Calendar';
// import Modal from '../Modal/Modal'
import {NavLink} from 'react-router-dom';
import './AddCombine.css';

class AddCombine extends Component {
    static contextType = UserContext;
    render(){
        return(
            <div className = {this.context.user.addCombShow ? 'addCombineShow' : 'addCombineClose'}>
                {/*<Modal>*/}
                <form onSubmit={(e) => {this.context.handleSubmitComb(e); this.context.addNewCombine()}}>
                <ul className="list">
                <li><span></span><input className="listItem" placeholder = '...' name="country" type="text" required onChange={this.context.handleChange} value={this.context.user.country}/>
                    {/*<select name='category'>*/}
                        {/*<option></option>*/}
                        {/*<option></option>*/}
                        {/*<option></option>*/}
                        {/*<option></option>*/}
                    {/*</select>*/}
                </li>
                    <li><span></span><input className="listItem" name="client" placeholder = '...' type="text" required onChange={this.context.handleChange} value={this.context.user.client}/></li>
                <li><span></span><input className="listItem" placeholder = '...' name="mine" type="text" required onChange={this.context.handleChange} value={this.context.user.mine}/></li>
                <li><span> </span><input className="listItem" placeholder = ' ...' name="install" required type="text" onChange={this.context.handleChange} value={this.context.user.install}/></li>
                <li><span> </span><input className="listItem" placeholder = ' ...' name="combName" required type="text" onChange={this.context.handleChange} value={this.context.user.combName}/></li>
                <li><span> </span><input className="listItem" placeholder = ' ...' name="combNumber" required type="text" onChange={this.context.handleChange} value={this.context.user.combNumber}/></li>
                <li><span> </span><CalendarNew startDate={this.context.user.date} handleChangeInstallDate={this.context.handleChangeInstallDate}/></li>
                {/*<li><span> </span><input className="listItem" placeholder = ' ...' name="installDate" required type="text" onChange={this.context.handleChange} value={this.context.user.installDate}/></li>*/}
                <li><span> </span><input className="listItem" placeholder = ' ...' name="warrantyTerm" required type="text" onChange={this.context.handleChange} value={this.context.user.warrantyTerm}/></li>
                {/*<li><span>  </span><CalendarNew startDate={this.context.user.date} handleChangeInstallDate={this.context.handleChangeInstallDate}/></li>*/}
                <li><span>  </span><input className="listItem" placeholder = '  ...' required name="startOperationDate" type="text" onChange={this.context.handleChange} value={this.context.user.startOperationDate}/></li>
                </ul>
                <button className = 'btnAddCombine'></button>
                </form>
            {/*<NavLink to="/mainpage">*/}
                {/*<button className = 'btnAddCombine'></button>*/}
            {/*</NavLink>*/}
                {/*</Modal>*/}
            </div>
        )
    }
}

export default AddCombine;





