import React from 'react'
import {UserContext} from '../UserContext';
import Card from '../Card/Card'
import './Grid.css'

const Grid = ({combProps}) => {

    return (
        <div className="grid_cards">
            {(combProps) ? combProps.map(el => <Card combProps={el}/>) : null}
            {/*{(JSON.parse(localStorage.getItem('combines'))) ? JSON.parse(localStorage.getItem('combines')).map(el => <Card combProps={el}/>) : null}*/}
            {/*{data.map(el => <Card img={'https://image.tmdb.org/t/p/w300/' + el.poster_path} title={el.title}  key = {el.id} id={el.id} favAddTo={favAddTo} wishAddTo={wishAddTo}/>)}*/}
        </div>
    )
}


export default Grid;



